import { Component, OnInit } from "@angular/core";
import { environment } from "src/environments/environment";
import { AuthenticationService, Credentials, LoginContext } from '../../../core/authentication/authentication.service';
import { Logger } from '../../../core/logger.service';
import { Router } from '@angular/router';
import { style } from '@angular/animations';
import { ConfirmationService } from 'src/app/shared/confirmdialog/confirmationservice';
import { StorageUtilService } from 'src/app/shared/utils/storage-util.service';
const log = new Logger('LoginComponent');
@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
})
export class LoginComponent implements OnInit {
  show: boolean;
  view: boolean;
  displayModal: boolean;
  username: string = "";
  password: string = "";
  appli1: string = "";
  isUsernameValid: boolean = false;
  isPasswordValid: boolean = false;
  isAppliValid: boolean = false;
  isValidForm: boolean = false;
  toggle: boolean;
  credentials: any;
  userDomainKey = "userdomain";
  change = false;
  name: any;
  avatar: any;
  changeImg = true;

  constructor(
    private router: Router,
    private authenticationService: AuthenticationService,
    private confirmationService: ConfirmationService) {
    this.show = false;
    this.toggle = false;
    this.view = false;

    if (StorageUtilService.hasItem(this.userDomainKey)) {
      this.credentials = StorageUtilService.getItem('userdomain');
      // console.log(this.credentials );
    }
  }


  onSubmit() {
    if (this.isValidForm && this.toggle === false) {
      this.authenticationService.login(this.username, this.password, this.appli1).then( val => {

        if (this.authenticationService.isLoggedIn) {
          console.log('login');
          const redirect = '/';
          if (this.authenticationService.isAuthenticated() === false) {
            // this.spinnerService.hide();
            console.log(this.authenticationService.isAuthenticated());
          } else {
            // Redirect the user
            this.router.navigate([redirect], { replaceUrl: true });
            // this.spinnerService.hide();
          }
        } else {
          console.log('belum login');
        }

      });
    } else if (this.isValidForm && this.toggle === true) {
      this.confirmationService.confirm({
        icon: './assets/img/gif/Scan-Finger-GEN2.gif',
        acceptIcon: null,
        rejectIcon: null,
        rejectVisible: false,
        acceptVisible: false, // sementara
        acceptLabel: 'YA',
        rejectLabel: 'BATAL',
        header:  '',
        message: 'Scan jari anda pada perangkat',
        closable: true,
        accept: () => {},
      });
      this.loading();
    }
  }

  wait(timeout) {
    return new Promise(resolve => {
        setTimeout(resolve, timeout);
    });
  }

  loading = async () => {
    await this.wait(5000);
    this.authenticationService.login(this.username, this.password, this.appli1).then( val => {

      if (this.authenticationService.isLoggedIn) {
        console.log('login');
        const redirect = '/';
        if (this.authenticationService.isAuthenticated() === false) {
          // this.spinnerService.hide();
          console.log(this.authenticationService.isAuthenticated());
        } else {
          // Redirect the user
          this.router.navigate([redirect], { replaceUrl: true });
          // this.spinnerService.hide();
        }
      } else {
        console.log('belum login');
      }
    });
  }

  onKlik() {
    if (document.getElementById('ifYes').style.display === 'block') {
      document.getElementById('ifYes').style.display = 'none';
      this.toggle = true;
    } else {
      document.getElementById('ifYes').style.display = 'block';
      this.toggle = false;
    }
    this.validateUsername();
    this.validateForm()
  }

  // click event function toggle
  showPassword() {
    this.show = true;
  }

  hidePassword() {
    this.show = false;
  }

  showAppli() {
    this.view = true;
  }

  hideAppli() {
    this.view = false;
  }

  ngOnInit() {
    // let data = this.encryptPass("123456");
    // alert(data);
    if (this.credentials !== undefined) {
      this.username = this.credentials.user_domain;
      this.name = this.credentials.user_name;
      this.isValidForm = true;
      if (this.credentials.avatar !== '') {
        this.avatar = this.credentials.avatar;
        this.changeImg = false;
        this.change = true;
      } else {
        this.changeImg = true;
        this.change = true;
      }
    } else {
      this.username = '';
      this.name = '';
    }

    this.onKlik();
  }

  validateUsername(): void {
    const pattern = RegExp(/^[\w-.]*$/);
    if (this.username.length >= 3) {
      this.isUsernameValid = true;
    } else {
      this.isUsernameValid = false;
    }
  }

  validatePassword(): void {
    const pattern = RegExp(/^[yz]*x[xyz]*$/);
    if (this.password.length >= 6) {
      this.isPasswordValid = true;
    } else {
      this.isPasswordValid = false;
    }
  }

  validateAppli(): void {
    const pattern = RegExp(/^[yz]*x[xyz]*$/);
    if (this.appli1.length >= 8) {
      this.isAppliValid = true;
    } else {
      this.isAppliValid = false;
    }
  }

  validateForm(): void {
    if (this.isUsernameValid && this.isPasswordValid && this.isAppliValid || this.isUsernameValid && document.getElementById('ifYes').style.display === 'none') {
      this.isValidForm = true;
    } else {
      this.isValidForm = false;
    }
  }

  onKey(event: any, type: string) {
    if (type === "username") {
      this.username = event.target.value;
      this.validateUsername();
      this.validateForm();
    } else if (type === "password") {
      this.password = event.target.value;
      this.validateUsername();
      this.validatePassword();
      this.validateForm();
    } else if (type === "appli1") {
      this.appli1 = event.target.value;
      this.validateUsername();
      this.validatePassword();
      this.validateAppli();
      this.validateForm();
    }
  }

  encryptPass(pass: String): string {
    let strByte: number[] = /* getBytes */ pass
      .split("")
      .map((s) => s.charCodeAt(0));

    console.log(strByte);
    let sb: { str: string; toString: Function } = {
      str: "",
      toString: function () {
        return this.str;
      },
    };
    var encryptKey = parseInt(environment.encryptKeyHexaString, 16);
    for (let i: number = 0; i < strByte.length; i++) {
      {
        console.log((strByte[i] & 255) ^ encryptKey);
        let v: number = (strByte[i] & 255) ^ encryptKey;
        if (v < 16) {
          console.log(v);
          /* append */ ((sb) => {
            sb.str = sb.str.concat(<any> "0");
            return sb;
          })(sb);
        }
        /* append */ ((sb) => {
          sb.str = sb.str.concat(<any> v.toString(16));
          return sb;
        })(sb);
      }
    }
    return sb.toString();
  }
}
